.contacts {
  &__address {
    margin-bottom: 8px;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
  }
  &__discr {
    display: block;
    margin-bottom: 24px;
    font-family: "Poppins", sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
  }
  &__discr:last-child {
    margin-bottom: 0;
  }
  &__fild {
    margin-bottom: 16px;
  }
  &__fild--last {
    margin-bottom: 32px;
  }
}
